import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router";
import { Link } from "react-router-dom";
import { ArrowCircleLeftIcon } from "@heroicons/react/outline"
import AnimeFact from "../components/AnimeFact"

const AnimeFacts = () => {
    let {anime_name} = useParams();
    const [facts,setFacts] = useState([]);
    
    useEffect(()=>{
        (async() => {
            const res = await axios.get(`https://anime-facts-rest-api.herokuapp.com/api/v1/${anime_name}`)
            setFacts(res.data);
        })();
    },[anime_name]);

    return(
        <div className="min-h-screen bg-black bg-opacity-95 text-gray-200">
            <div className="flex p-6 items-center">
                <Link to="/"><ArrowCircleLeftIcon className="h-10"/></Link>
                <h1 className="text-4xl mx-6">{anime_name.toUpperCase()} FACTS</h1>
            </div>
            <div className="md:flex justify-center">
                <img src={facts.img} alt="anime_image" className="h-96 w-auto md:top-24 md:sticky m-4"></img>
                <div>
                    {
                        facts.data !== undefined && facts.data.map((eachfact)=> {
                            return <AnimeFact fact={eachfact.fact} key={eachfact.fact_id}/>
                        })
                    }
                </div>
            </div>
        </div>
    )
}

export default AnimeFacts;