import axios from "axios";
import { useEffect, useState } from "react";
import Tab from "../components/Tab"

const Home = () => {
  const [anime,setAnime] = useState([]);
  const [searchResult, setSearchResult] = useState("");

  useEffect (()=>{
    const getData = async () => {
      const res = await axios.get("https://anime-facts-rest-api.herokuapp.com/api/v1")
      setAnime(res.data.data);
    }
    getData();
  },[])

  return (
   <div className="flex flex-col items-center font-bold text-xl bg-black bg-opacity-95 text-gray-600 min-h-screen">
     <h1 className="m-4 text-3xl text-gray-200">Anime Facts</h1>
     <input 
      className="outline-none bg-gray-100 rounded ring-2 ring-gray-500 p-2 font-medium transition-all ease-in-out"
      placeholder="Search anime..."
      onChange={(event)=>{
        // const {value} = event.target;
        // const filteredAnime = anime.filter((anime)=>{
        //   const {anime_name} = anime
        //   return anime_name.toLowerCase().includes(value.toLowerCase())
        // })
        // setAnime(filteredAnime);
        setSearchResult(event.target.value);
      }}
     />
     <div className="flex justify-center flex-wrap m-4">
      {
        anime.filter((eachitem)=>{
          const {anime_name} = eachitem;
          if (searchResult === ""){
            return eachitem;
          }else if (anime_name.toLowerCase().includes(searchResult.toLowerCase())){
            return eachitem;
          }
        }).map((eachanime,i)=>{
          return <Tab name={eachanime.anime_name} img={eachanime.anime_img} key={i}/>
        })
      }
     </div>
     
   </div>  
  );
}

export default Home;
