import { useLocation } from "react-router";
import { Link } from "react-router-dom";

const Header = () => {
    const {pathname} = useLocation();
    
    return(
        <div className="flex justify-between p-3">
            <h1 className="md:mx-4 mx-1">Site.com</h1>
            <div className="flex">
                <Link to="/" className={`mx-4 cursor-pointer hover:text-blue-400 ${pathname === "/" && "underline"}`}>Home</Link>
                <Link to="/about" className={`mx-4 cursor-pointer hover:text-blue-400 ${pathname === "/about" && "underline"}`}>About</Link>
                <Link to="/services" className={`mx-4 cursor-pointer hover:text-blue-400 ${pathname === "/services" && "underline"}`}>Services</Link>
            </div>
        </div>
    )
}

export default Header;
