import { Link } from "react-router-dom";

const Tab = (props) => {
    return(
        <Link to={`/${props.name}`} className="m-6 hover:text-gray-200 cursor-pointer transition-all ease-in-out">
            <img src={props.img} alt="animeimage" className="h-72 w-48 rounded filter shadow ring-2 ring-white hover:ring-blue-300"/>
            <h1 className="mt-3">{props.name}</h1>
        </Link>
    );
}

export default Tab;
